html {
    box-sizing: border-box;
    max-width: 100%;
}

*,
*::after,
*::before {
    box-sizing: inherit;
}

body {
    margin: 0;
    font-family: 'Roboto Slab', serif;
    font-size: clamp(0.75rem, calc(0.5rem + 1vw), 1.25rem);
}

a {
    color: inherit;
    text-decoration: none;
}

h1,
h2,
h3,
h4 {
    font-family: 'Roboto', sans-serif;
}